.callback-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    max-width: 600px;
    width: 100%;

    &__label{
        width: 100%;
    }

    &__input{
        width: 100%;
        height: 44px;
        text-indent: 16px;
        border: 1px solid var(--border-b);
        
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: clamp(14px, 1.067vw, 16px);
        line-height: 140%;
        color: rgba(var(--cl-c),1.0);

        &::placeholder{
        color: rgba(var(--cl-f),1.0);
        }

        @include small-tablet{
            text-indent: 20px;
        }
    }

    &__button{
        height: 50px;
        width: clamp(300px, 22.667vw, 340px);
        margin-top: clamp(10px, 2vw, 30px);
    }
}