    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    background: #F2F2F2;
    max-width: 720px;
    width: 100%;
    padding: clamp(33px, 2.667vw, 40px) clamp(10px, 4vw, 60px) clamp(30px, 3.333vw, 50px);
