ter%3E%3C/defs%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-size: cover;
        width: clamp(331px, 36.467vw, 547px);
         height: clamp(331px, 36.467vw, 547px);
        position: absolute;
         content: '';
         z-index: -1;
         left: -1%;
         top: 50%;
         transform: translate(-40%, -50%);

         @include laptop{
            left: 50%;
            top: initial;
            bottom: 0;
            transform: translate(-50%, 25%);
         }

         @include mobile{
            transform: translate(-50%, 21%);
         }
     }

     @include small-tablet{
        max-width: initial;
        justify-self: start;
        width: calc(100% + (var(--container-offset) * 2));
        margin-left: calc((var(--container-offset) * -1));
        padding: clamp(34px, 2.667vw, 40px) var(--container-offset);
    }
    &__label{
        width: 100%;
    }

    &__input{
        height: 44px;
        text-indent: clamp(16px, 1.467vw, 22px);
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: clamp(14px, 1.067vw, 16px);
        line-height: 130%;
        color: rgba(var(--cl-c),1.0);

        &::placeholder{
        color: rgba(var(--cl-f),1.0);
        }

    }

    &__title{
        margin-bottom: clamp(10px, 1.333vw, 20px);
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: clamp(16px, 1.6vw, 24px);
        line-height: 150%;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #1D252D;

    }

    &__button{
        margin-top: clamp(10px, 1.333vw, 20px);
        width: clamp(300px, 21.333vw, 320px);
        height: 59px;

        @include ios{
            width: 100%;
        }
    }
}