.contact-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    background: rgba(var(--bg-l),1.0);
    padding: clamp(34px, 2.667vw, 40px) clamp(30px, 4vw, 60px);
    position: relative;
    width: clamp(500px, 48vw, 720px);
    justify-self: end;

    @include tablet{
        justify-self: center;
    }

    @include small-tablet{
        // width: calc(100% + 30px);
        justify-self: start;
        width: calc(100% + (var(--container-offset) * 2));
        margin-left: calc((var(--container-offset) * -1));
        padding: clamp(34px, 2.667vw, 40px) var(--container-offset);
    }

