er%3E%3C/defs%3E%3C/svg%3E");
       background-repeat: no-repeat;
       background-size: cover;
       width: 547px;
        height: 547px;
       position: absolute;
        content: '';
        z-index: -1;
        left: 0;
        top: 50%;
        transform: translate(-40%, -50%);
    }

    &__label{
        width: 100%;
    }

    &__title{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: clamp(16px, 1.6vw, 24px);
        line-height: 150%;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: rgba(var(--cl-c),1.0);
    }

    &__input{
        height: 44px;
        text-indent: 16px;
        width: 100%;
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: clamp(14px, 1.067vw, 16px);
        line-height: 130%;
        color: rgba(var(--cl-c),1.0);

        &::placeholder{
        color: rgba(var(--cl-f),1.0);
        }
    }

    &__button{
        width: clamp(300px, 21.333vw, 320px);
    }

    &__validation{
        width: 100%;
        justify-content: flex-start;
        display: flex;
        align-items: center;
        grid-gap: clamp(14px, 1.333vw, 20px);

        &-icon{
            height: clamp(40px, 6.133vw, 92px);

            img,picture{
                width: 100%;
                height: 100%;
            }
        }
    }
}